
html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Raleway', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
a {
  text-decoration: none;
  color: #FD971F;
  cursor: pointer;
  transition: color .2s;
}
a:hover {
  color: #A6E22E;
}
#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  height: 100%;
  overflow-x: auto;
  overflow-y: hidden;
}
#app .page-header {
  -ms-flex: none;
      flex: none;
}
#app .page-content {
  -ms-flex: 1;
      flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
#app .page-content .content-container {
  background-color: #242424;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  width: 100%;
  min-width: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
#app .page-content .content-container .main-content {
  background: #eee;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  min-width: 300px;
  padding: 20px;
}
#app ul.list-unstyled {
  list-style: none;
}



